import React, { createContext, useReducer, useEffect } from 'react';
import { useQuery } from '@apollo/client';
import { reducer } from './reducer';
import { SET_AUTH_USER } from './actionTypes';
import { GET_AUTH_USER_INFO, WELCOME_QUERY } from 'graphql/queries/user';
import { AUTH_TOKEN, errorMessages } from 'utils/constants';

const initialState = {
  isAuthenticated: false,
  user: {
    name: 'Shen Zhi',
    city: 'Los Angeles',
    country: 'USA',
    timezone: 'GTM-7',
    avatar: '/images/avatars/avatar_11.png',
  },
};
export const AuthContext = createContext(initialState);
const token = localStorage[AUTH_TOKEN];
let query = token ? GET_AUTH_USER_INFO : WELCOME_QUERY;

export const AuthProvider = ({ children }) => {
  const { data, error } = useQuery(query, {
    errorPolicy: 'none',
  });
  const [state, dispatch] = useReducer(reducer, initialState);
  const { Provider } = AuthContext;
  useEffect(() => {
    if (data && data.me) {
      dispatch({ type: SET_AUTH_USER, payload: data.me });
    }
  }, [data]);
  useEffect(() => {
    if (error) {
      switch (error.message) {
        case errorMessages.noPermission:
          console.log('nP');
          if (window.location.pathname !== '/sign-in') {
            window.location.replace('/sign-in');
          }
          break;
        case errorMessages.decodeToken:
          console.log('dT');
          localStorage.clear();
          window.location.replace('/sign-in');
          break;
        default:
          console.log('def');
          break;
      }
    }
  }, [error]);
  return (
    <Provider
      value={{
        ...state,
        setAuthUser: (userInfo) =>
          dispatch({ type: SET_AUTH_USER, payload: userInfo }),
        dispatch,
      }}
    >
      {children}
    </Provider>
  );
};
