import { gql } from '@apollo/client';

export const STORE_MUTATION = gql`
  mutation StoreMutation(
    $amount: Float!
    $recordType: String!
    $isProperty: Boolean!
    $isInflow: Boolean!
    $actionDate: Date!
    $description: String!
  ) {
    createStore(
      amount: $amount
      recordType: $recordType
      isProperty: $isProperty
      isInflow: $isInflow
      actionDate: $actionDate
      description: $description
    ) {
      message
    }
  }
`;
