import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';

import { TableToolbar } from 'components';
import { UsersTable } from './UsersTable';
import { useQuery } from '@apollo/client';
import { GET_SYSTEM_USERS } from 'graphql/queries';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(3),
  },
  content: {
    marginTop: theme.spacing(2),
  },
}));

const initialState = {
  pageData: [],
  totalCount: 0,
  numPages: 0,
};
export const UserList = () => {
  const classes = useStyles();
  const [users, setUsers] = useState(initialState);
  const [selectedUsers, setSelectedUsers] = useState([]);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [page, setPage] = useState(0);
  const [search, setSearch] = useState('');

  const { loading, data } = useQuery(GET_SYSTEM_USERS, {
    variables: { search, pageCount: rowsPerPage, pageNumber: page },
  });
  useEffect(() => {
    if (data && data.users) {
      setUsers(data.users);
    }
  }, [data]);
  const handleSelectAll = (event) => {
    let selectedUsers;

    if (event.target.checked) {
      selectedUsers = users.pageData.map((item) => item.id);
    } else {
      selectedUsers = [];
    }
    setSelectedUsers(selectedUsers);
  };

  const handleSelectOne = (event, id) => {
    const selectedIndex = selectedUsers.indexOf(id);
    let newSelectedUsers = [];

    if (selectedIndex === -1) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers, id);
    } else if (selectedIndex === 0) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers.slice(1));
    } else if (selectedIndex === selectedUsers.length - 1) {
      newSelectedUsers = newSelectedUsers.concat(selectedUsers.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelectedUsers = newSelectedUsers.concat(
        selectedUsers.slice(0, selectedIndex),
        selectedUsers.slice(selectedIndex + 1)
      );
    }

    setSelectedUsers(newSelectedUsers);
  };

  const handlePageChange = (event, page) => {
    setPage(page);
  };

  const handleRowsPerPageChange = ({ target }) => {
    setPage(0);
    setRowsPerPage(target.value);
  };
  const handleSearchChange = ({ target }) => {
    setPage(0);
    setSearch(target.value);
  };
  return (
    <div className={classes.root}>
      <TableToolbar
        btnLabel='Add a new user'
        searchHolder='Search a user'
        onChange={handleSearchChange}
      />
      <div className={classes.content}>
        <UsersTable
          users={users.pageData}
          usersCount={users.totalCount}
          loading={loading}
          selectedUsers={selectedUsers}
          rowsPerPage={rowsPerPage}
          page={page}
          pageOptions={[5, 10, 25, 50, 100]}
          handleSelectOne={handleSelectOne}
          handleSelectAll={handleSelectAll}
          handlePageChange={handlePageChange}
          handleRowsPerPageChange={handleRowsPerPageChange}
        />
      </div>
    </div>
  );
};
