import React from 'react';
import {
  TextField,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  DialogContentText,
  Button,
  Grid,
  FormControlLabel,
  Switch,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@material-ui/core';
import { storeChoices } from 'utils/constants';

export const AddRecord = ({
  open,
  setOpen,
  values = {},
  onHandleChange,
  onSave,
  loading = false,
}) => {
  return (
    <Dialog open={open} onClose={setOpen} aria-labelledby='form-dialog-title'>
      <DialogTitle id='add-dialog'>Add a record</DialogTitle>
      <DialogContent>
        <DialogContentText>
          To add a record to your store, please fill all required details below.
        </DialogContentText>
        <Grid container spacing={3}>
          <Grid item md={4} xs={12}>
            <FormControl variant='outlined' fullWidth>
              <InputLabel id='record-type'>Record type</InputLabel>
              <Select
                labelId='record-type'
                value={values.recordType}
                name='recordType'
                onChange={onHandleChange}
              >
                <MenuItem value=''>---</MenuItem>
                {storeChoices.map(({ id, name }, choiceIdx) => (
                  <MenuItem value={id} key={choiceIdx}>
                    {name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item md={4} xs={12}>
            <FormControlLabel
              control={
                <Switch
                  name='isProperty'
                  checked={values.isProperty}
                  onChange={onHandleChange}
                />
              }
              label='Is for property?'
            />
          </Grid>
          <Grid item md={4} xs={12}>
            <FormControlLabel
              control={
                <Switch
                  name='isInflow'
                  checked={values.isInflow}
                  onChange={onHandleChange}
                />
              }
              label='Is income?'
            />
          </Grid>
          <Grid item md={6} xs={12}>
            <TextField
              fullWidth
              margin='dense'
              variant='outlined'
              type='date'
              name='actionDate'
              value={values.actionDate}
              onChange={onHandleChange}
            />
          </Grid>
          <Grid item md={6} xs={12}>
            <TextField
              fullWidth
              label='Amount'
              margin='dense'
              type='number'
              variant='outlined'
              name='amount'
              value={values.amount}
              onChange={onHandleChange}
            />
          </Grid>
          <Grid item md={12} xs={12}>
            <TextField
              autoFocus
              margin='dense'
              variant='outlined'
              label='Record description'
              fullWidth
              name='description'
              value={values.description}
              onChange={onHandleChange}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={setOpen} color='primary'>
          Cancel
        </Button>
        <Button onClick={onSave} color='primary' disabled={loading}>
          {loading ? 'Saving data,...' : 'Save record'}
        </Button>
      </DialogActions>
    </Dialog>
  );
};
