import { gql } from '@apollo/client';

export const DASHBOARD_INFO_QUERY = gql`
  query {
    totalInflow
    totalOutflow
    storeCount
    storeAggregate {
      inflow
      outflow
      percent
    }
    propertiesDetail {
      count
      totalAmount
    }
  }
`;
export const GET_STORES_QUERY = gql`
  query Stores($search: String!, $pageCount: Int!, $pageNumber: Int!) {
    stores(search: $search, pageCount: $pageCount, pageNumber: $pageNumber) {
      pageData {
        id
        amount
        description
        isProperty
        isInflow
        actionDate
      }
      numPages
      totalCount
    }
  }
`;
export const GET_MONTLY_STORES = gql`
  query {
    monthlyStore {
      label
      value
    }
  }
`;
export const GET_STORE_RATIO = gql`
  query {
    storeRatio {
      inflow
      outflow
      percent
    }
  }
`;
