import React, { useState, useEffect } from 'react';
import { SearchInput, CustomisedTable } from 'components/common';
import { Button } from '@material-ui/core';
import { transactionStyles } from './transactionStyles';
import { transactionColumns } from 'components/columns';
import { useQuery, useMutation } from '@apollo/client';
import { GET_STORES_QUERY } from 'graphql/queries';
import { StoreImport, AddRecord } from 'components/Models';
import { recordInitials } from 'utils/initialStates';
import { STORE_MUTATION } from 'graphql/mutations/store';
import { toast } from 'react-toastify';

const initialStates = {
  pageData: [],
  totalCount: 0,
};
export const TransactionView = () => {
  const classes = transactionStyles();

  const [stores, setStores] = useState(initialStates);
  const [recordValue, setRecordValue] = useState(recordInitials);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(1);
  const [search, setSearch] = useState('');
  const [openImport, setOpenImport] = useState(false);
  const [openAddRecord, setOpenAddRecord] = useState(false);

  const { loading, data, refetch } = useQuery(GET_STORES_QUERY, {
    variables: { search, pageCount: rowsPerPage, pageNumber: page },
  });
  const [
    createStore,
    { data: createData, loading: createLoading },
  ] = useMutation(STORE_MUTATION, { errorPolicy: 'ignore' });
  useEffect(() => {
    if (data && data.stores) {
      setStores(data.stores);
    }
  }, [data]);
  useEffect(() => {
    if (createData) {
      setRecordValue(recordInitials);
      refetch();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [createData]);
  const handlePageChange = (event, page) => {
    setPage(page + 1);
  };

  const handleRowsPerPageChange = ({ target }) => {
    setPage(1);
    setRowsPerPage(target.value);
  };
  const handleSearchChange = ({ target }) => {
    setPage(1);
    setSearch(target.value);
  };
  const onHandleChange = ({ target: { name, value, checked } }) => {
    const inputValue =
      name === 'isProperty' || name === 'isInflow' ? checked : value;
    setRecordValue({ ...recordValue, [name]: inputValue });
  };
  const onSaveRecord = () => {
    recordValue.amount = parseFloat(recordValue.amount) || 0;
    if (recordValue.actionDate === '') {
      toast('Select date');
      return;
    }
    createStore({ variables: recordValue });
  };
  return (
    <div className={classes.root}>
      <div className={classes.row}>
        <StoreImport open={openImport} setOpen={() => setOpenImport(false)} />
        <AddRecord
          open={openAddRecord}
          setOpen={() => setOpenAddRecord(false)}
          values={recordValue}
          onHandleChange={onHandleChange}
          onSave={onSaveRecord}
          loading={createLoading}
        />
        <span className={classes.spacer} />
        <Button
          className={classes.importButton}
          onClick={() => setOpenImport(true)}
        >
          Import
        </Button>
        <Button className={classes.exportButton}>Export</Button>
        <Button
          color='primary'
          variant='contained'
          onClick={() => setOpenAddRecord(true)}
        >
          Add a new record
        </Button>
      </div>
      <div className={classes.row}>
        <SearchInput
          className={classes.searchInput}
          placeholder='Search user'
          onChange={handleSearchChange}
        />
      </div>
      <div className={classes.content}>
        <CustomisedTable
          data={stores.pageData}
          dataCount={stores.totalCount}
          page={page - 1}
          columns={transactionColumns}
          loading={loading}
          rowsPerPage={rowsPerPage}
          pageOptions={[5, 10, 25, 50, 100]}
          handlePageChange={handlePageChange}
          handleRowsPerPageChange={handleRowsPerPageChange}
        />
      </div>
    </div>
  );
};
