import { gql } from '@apollo/client';

export const GET_PROPERTIES_QUERY = gql`
  query Properties($search: String!, $pageCount: Int!, $pageNumber: Int!) {
    properties(
      search: $search
      pageCount: $pageCount
      pageNumber: $pageNumber
    ) {
      pageData {
        id
        name
        price
        description
        coverImage
        createdAt
        detailsCounts
        propDetails {
          id
          title
          type
          amount
        }
      }
      numPages
      totalCount
    }
    propertiesDetail {
      count
      totalAmount
    }
  }
`;
