import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardActions,
  CardHeader,
  CardContent,
  Button,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from '@material-ui/core';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import { useQuery, useMutation } from '@apollo/client';
import { GET_STORES_QUERY } from 'graphql/queries';
import { Loading } from 'components/Loading';
import { NoDisplayData } from 'components/NoDisplayData';
import { recordInitials } from 'utils/initialStates';
import { STORE_MUTATION } from 'graphql/mutations/store';
import { toast } from 'react-toastify';
import { AddRecord } from 'components/Models';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {},
  content: {
    padding: 0,
  },
  inner: {
    minWidth: 800,
  },
  statusContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  status: {
    marginRight: theme.spacing(1),
  },
  actions: {
    justifyContent: 'flex-end',
  },
}));

export const TransactionsDash = ({ className, ...rest }) => {
  const classes = useStyles();
  const [stores, setStores] = useState([]);
  const [recordValue, setRecordValue] = useState(recordInitials);
  const [openAddRecord, setOpenAddRecord] = useState(false);

  const { loading, data, refetch } = useQuery(GET_STORES_QUERY, {
    variables: { search: '', pageCount: 5, pageNumber: 1 },
  });
  const [
    createStore,
    { data: createData, loading: createLoading },
  ] = useMutation(STORE_MUTATION, { errorPolicy: 'ignore' });
  useEffect(() => {
    if (data && data.stores) {
      setStores(data.stores.pageData);
    }
  }, [data]);
  useEffect(() => {
    if (createData) {
      setRecordValue(recordInitials);
      refetch();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [createData]);
  const onHandleChange = ({ target: { name, value, checked } }) => {
    const inputValue =
      name === 'isProperty' || name === 'isInflow' ? checked : value;
    setRecordValue({ ...recordValue, [name]: inputValue });
  };
  const onSaveRecord = () => {
    recordValue.amount = parseFloat(recordValue.amount) || 0;
    if (recordValue.actionDate === '') {
      toast('Select date');
      return;
    }
    createStore({ variables: recordValue });
  };
  return (
    <Card {...rest} className={clsx(classes.root, className)}>
      <AddRecord
        open={openAddRecord}
        setOpen={() => setOpenAddRecord(false)}
        values={recordValue}
        onHandleChange={onHandleChange}
        onSave={onSaveRecord}
        loading={createLoading}
      />
      <CardHeader
        action={
          <Button
            color='primary'
            size='small'
            variant='outlined'
            onClick={() => setOpenAddRecord(true)}
          >
            New entry
          </Button>
        }
        title='Latest stores'
      />
      <Divider />
      <CardContent className={classes.content}>
        {loading ? (
          <Loading />
        ) : stores.length ? (
          <PerfectScrollbar>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>#</TableCell>
                  <TableCell>Amount</TableCell>
                  <TableCell>Description</TableCell>
                  <TableCell>Created at</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {stores.map((store, idx) => (
                  <TableRow hover key={idx}>
                    <TableCell>{idx + 1}</TableCell>
                    <TableCell>{store.amount}</TableCell>
                    <TableCell>{store.description}</TableCell>
                    <TableCell>
                      {moment(store.actionDate).format('DD/MM/YYYY')}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </PerfectScrollbar>
        ) : (
          <NoDisplayData />
        )}
      </CardContent>
      {stores.length > 4 && (
        <>
          <Divider />
          <CardActions className={classes.actions}>
            <Button
              color='primary'
              size='small'
              variant='text'
              component={Link}
              to='/transactions'
            >
              View all <ArrowRightIcon />
            </Button>
          </CardActions>
        </>
      )}
    </Card>
  );
};
